.container {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    padding: 17% 5%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    background-color: #030B32;
    z-index: 0;
    background-image: linear-gradient(rgba(0,8,51,0.7),rgba(0,8,51,0.7)), url(AI.png);
    background-position: center;
    background-size: cover;
}
.container * {
    font-family: 'Poppins', sans-serif;
}

h1 {
    color: whitesmoke;
    font-size: 60px;
    font-family: 'Poppins', sans-serif;
}
.youtube-search {
    display: flex;
    justify-content: center;
    width: 100%;
}
.search-bar {
    width: 100%;
    max-width: 700px;
    background: whitesmoke;
    display: flex;
    align-items: center;
    border-radius: 60px;
    padding: 5px 10px;
    backdrop-filter: blur(4px) saturate(180%);
}
.search-bar input {
    background: transparent;
    flex: 1;
    border: 0;
    outline: none;
    padding: 10px 20px;
    font-size: 20px;
    color: grey
}
.search-bar input:focus {
    background-color: white;
}
::placeholder {
    color: grey;
}
.search-bar button img{
    width: 25px;

}
.search-bar button {
    border: 0px;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    background: #58629b;
    cursor: pointer;
}


.no-script, .url-error{
    position: absolute;
    left: calc(50vw - 350px);
    top: 64vh;
    height: 80px;
    margin: 20px;
    width: 560px;
    font-size: 20px;
    color: red;
    font-family: 'Poppins', sans-serif;
    line-height: 1.5em;
    background-color: rgba(255, 255, 255, 0.35);
    padding: 10px;
    border-radius: 8px;
    /* display: flex;
    justify-content: center; */
}
.no-script p, .url-error p{
    display: flex;
    justify-content: center;
}
